.item {
  border: 1px solid #1467C1;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  background: #d8e5eb;
  display: grid;
  grid-template-columns: 65px 1fr;
  color: #1467C1;
}

.item img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: 0 0 0 2px white, 0 0 0 3px #1467C1;
  margin-bottom: 10px;
}

.imgWrapper {
}

.likeWrapper {
  border: 1px solid #1467C1;
  padding: 2px 5px;
  display: inline-block;
  border-radius: 10px;
  background: white;
}

